<template>
    <section>
        <div v-if="isStripePaymentGateway">
            <form ref="paymentForm" @submit.prevent="submitPayment">
                <div class="subscription-container whitebg">

                    <div class="content" style="display:flex;">

                        <div class="accordion shop-accordin nominus" style="width: 70%; margin-right: 20px">

                            <div class="ui-accordion-content ui-corner-bottom ui-widget-content">
                                <div class="form_fields pack_margtop_bot divpaymentonline">

                                    <div class="payment-form_hs form_fields">

                                        <div class="form-group">
                                            <label for="cardholder-name">Name On Card:</label>
                                            <input v-model="billingDetails.name" type="text" placeholder="Name On Card"
                                                class="stripe-input w-100" />
                                        </div>

                                        <div class="form-group">
                                            <label>Card Number: <span class="mandatory">*</span></label>
                                            <div id="card-number-element" class="stripe-input"></div>
                                        </div>

                                        <div class="form-group">
                                            <label>Expiry Date: <span class="mandatory">*</span></label>
                                            <div id="card-expiry-element" class="stripe-input"></div>
                                        </div>

                                        <div class="form-group">
                                            <label>CVC: <span class="mandatory">*</span></label>
                                            <div id="card-cvc-element" class="stripe-input"></div>
                                        </div>

                                        <div id="card-errors" class="stripe-error">{{ errorMessage }}</div>

                                    </div>

                                    <div class="cont_99P">
                                        <span class="mandatory pr font12">Fields marked with * are mandatory.</span>
                                    </div>

                                </div>
                            </div>

                        </div>

                    </div>
                </div>

                <!-- Hidden fields to be submitted -->
                <input type="hidden" ref="stripeToken">
                <input type="hidden" ref="hdnCardType">
                <input type="hidden" ref="hdnNameonCard">
                <input type="hidden" ref="hdnCardNumber">
                <input type="hidden" ref="hdnCardMonth">
                <input type="hidden" ref="hdnCardYear">
                <input type="hidden" ref="hdnCvv">

                <button type="submit" class="btn btn-primary">Submit</button>

            </form>
        </div>
    </section>
</template>

<script>
import DataService from "../../../services/DataService";
export default {

    data() {
        return {
            stripe: null,
            elements: null,
            cardNumber: null,
            cardExpiry: null,
            cardCvc: null,
            errorMessage: "",
            billingDetails: {
                name: ""
            },

            isStripePaymentGateway: true,
            stripePublishableKey:
                "pk_test_51RjfwB4C6gUvp1ei45F8RMBEUh9vCrFMZgb6Qgrs78Rs9ED9gX2H0HpVm0Pv7NlHiGOc7wdJ1OtH7phwoxpJMgMY00tzECaTbs"
        };
    },

    async mounted() {
        debugger;

        if (!this.isStripePaymentGateway) return;

        // Load Stripe script before initializing
        await this.loadStripeScript();

        this.initializeStripe();
    },

    methods: {
        initializeStripe() {
            debugger
            // Initialize Stripe and Elements
            this.stripe = Stripe(this.stripePublishableKey);
            this.elements = this.stripe.elements();

            const style = {
                base: {
                    fontSize: "16px",
                    fontFamily: "Arial, sans-serif",
                    color: "#212529",
                    "::placeholder": { color: "#888" }
                },
                invalid: { color: "#dc3545" }
            };

            // Create elements
            this.cardNumber = this.elements.create("cardNumber", { style });
            this.cardExpiry = this.elements.create("cardExpiry", { style });
            this.cardCvc = this.elements.create("cardCvc", { style });

            // Mount elements
            this.cardNumber.mount("#card-number-element");
            this.cardExpiry.mount("#card-expiry-element");
            this.cardCvc.mount("#card-cvc-element");

            // Handle errors
            [this.cardNumber, this.cardExpiry, this.cardCvc].forEach(el => {
                el.on("change", evt => {
                    this.errorMessage = evt.error ? evt.error.message : "";
                });
            });
        },

        async submitPayment() {
            debugger
            //if (window.showLoader) showLoader();
            console.log("this.cardNumber:", this.cardNumber);

            const { token, error } = await this.stripe.createToken(this.cardNumber, {
                name: this.billingDetails.name
            });

            if (error) {
                //if (window.hideLoader) hideLoader();
                this.errorMessage = this.mapStripeError(error);
                return;
            }

            // Fill hidden fields
            this.$refs.stripeToken.value = token.id;
            this.$refs.hdnCardType.value = token.card.brand;
            this.$refs.hdnNameonCard.value = token.card.name;
            this.$refs.hdnCardNumber.value = token.card.last4;
            this.$refs.hdnCardMonth.value = token.card.exp_month;
            this.$refs.hdnCardYear.value = token.card.exp_year;
            this.$refs.hdnCvv.value = 123;

            var Obj = {
                CardMonth: this.$refs.hdnCardMonth.value,
                CardNum: this.$refs.hdnCardNumber.value,
                CardType: this.$refs.hdnCardType.value,
                CardYear: this.$refs.hdnCardYear.value,
                CompanyName: 'UtilityBilling',
                CustomerId: '',
                InvoiceId: 0,
                NameOnCard: this.$refs.hdnNameonCard.value,
                NetAmount: 100,
                PaidAmount: 100,
                PaymentGatewayCode: 'Stripe',
                RefId: '',
                BillIdEnc: 0,
                Description: 'Wallet Recharge',
                StripeToken : this.$refs.stripeToken.value,
                UniqueId: null,
                TransactionId: null
            }
            var JsonObj = JSON.stringify(Obj);

            // Submit form
            //this.$refs.paymentForm.submit();

            DataService.MakeRealPartialpayment(JsonObj).then(function (response) {
                vm.isLoading = false;         


                if (response.data.status == 1) {

                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Payment")]), "success", true, vm.$t("Alert"));              
                                                      
                } else if (response.data.status == 2) {
                    
                    vm.ShowAlert(vm.$t("InvalidLoginCredentials"), "failure", true, vm.$t('Alert'));               
                                      
                    
                } else {                                   
                    vm.IsRechargeReceiptShow = true;                   
                    
                }

            });
        },

        mapStripeError(error) {
            debugger
            const codes = {
                incomplete_number: "Please enter a complete card number.",
                invalid_number: "The card number you entered is invalid.",
                incomplete_cvc: "Please enter your card's CVC.",
                invalid_cvc: "The CVC code you entered is incorrect.",
                incomplete_expiry: "Please enter the full expiration date.",
                invalid_expiry_year_past: "Your card's expiration year is in the past.",
                card_declined: "Your card was declined. Please try another payment method.",
                expired_card: "Your card has expired."
            };

            return codes[error.code] || error.message || "Something went wrong.";
        },

        loadStripeScript() {
            debugger
            return new Promise((resolve, reject) => {
                // If already loaded, resolve immediately
                if (document.querySelector('script[src="https://js.stripe.com/v3/"]')) {
                    resolve();
                    return;
                }

                const script = document.createElement("script");
                script.src = "https://js.stripe.com/v3/";
                script.onload = () => resolve();
                script.onerror = () => reject("Stripe failed to load");
                document.head.appendChild(script);
            });
        }
    }
};
</script>
